@import url(https://fonts.googleapis.com/css2?family=Merriweather&family=Public+Sans:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather&family=Public+Sans:wght@200&display=swap);

.contact-page {
    margin: 10%;
    border: 2px solid rgb(226, 219, 219);
    box-shadow: 5px 3px 5px 3px lightgrey;
    width: 80%;
}

.contact-container {
    display: flex;
    grid-gap: 20px;
    gap: 20px;
    margin-bottom: 10%;
}

.form-container {
    display: flex;
}

.contact-header {
    display: flex;
    margin-left: 30%;
    padding: 10%;
    text-decoration: underline;
    line-height: 2em;
    text-underline-position: under;
    color: #4A99D3;
}

.contact-message {
    padding-bottom: 1rem;
}

.form-grid {
    margin-left: 8%;
    display: grid;
    grid-template-columns: 500px 500px;
}

.form {
    margin-left: 10%;
    padding: 10px;
    line-height: 2em;
}

#button {
    background-color: #4A99D3;
}

.icon-info-map {
    /* border: 2px solid red; */
    line-height: 2em;
    padding: 10px;
    margin-left: 30%;
}

.map-grid {
    margin-top: 10%;
    grid-column-start: 1;
    grid-column-end: 3;

}
.footer {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  font-size: small;
  font-family: Arial, Helvetica, sans-serif;
  margin-left: 5%;
  margin-right: 5%;
}

.footer-info  {
  display: flex;
  justify-content: center;
}

.footer-info-column {
  display: flex;
  flex-direction: column;
  width: 20%;
}

.footer-item {
  text-align: start;
}

.footer-seperator {
  flex-grow: 1;
  border: none;
  height: 1px;
  background-color: black;
  width: 100%;
}

.footer-image {
  width: 100px;
  height: 100px;
}

.social-icons {
  text-align: right;
  padding-right: 20px;
}

.bottom-bar {
  display: flex;
}

.social-icon-label {
  display: flex;
  margin-left: auto;
  float: right;
  padding-right: 20px;
}

.copyright {
  display: flex;
  float: left;
}

.footer-links li {
  list-style-type: none;
  text-align: right;
  padding-right: 20px;
}

.footer-links a {
  color: black;
  text-decoration: none;

}
*{
  margin: 0;
  padding: 0;
}

.navbar {
  display: flex;
  margin-top: -50px;
  margin-bottom: 0;
  padding: 0;
  height: 80px;
  font-size: 2rem;
}

.nav-container {
  margin-top: 0;
  margin-bottom: 50px;
}

.menu-bars {
  display: flex;
  margin-left: 5rem;
}

.a-nav {
  transition: .4s;
  text-decoration: none;
  color: #000 !important;
}

.a-nav:hover {
  color: rgb(18, 80, 194) !important;
}

.ul-nav {
  display: flex;
  margin-right: 5%;
  list-style-type: none;
  transition: all .4s ease-in-out;
  font-size: 15px;
}


.ul-nav li {
  padding: 5px;
}


.container {
  display: flex;
  grid-gap: 50px;
  gap: 50px;
  align-items: center;
  justify-content: center;
  /* flex-wrap: wrap; */
}

#primary-header-one {
  font-family: "Merriweather", Arial;
  font-weight: bold;
  font-size: 50px;
  color: #373f41;
}

#primary-header-three {
  color: #3c64b1;
}

#primary-paragraph {
  font-family: "Public Sans", sans-serif;
  color: #737b7d;
}

#primary-button {
  position: relative;
  display: block;
  border: 2px solid #3c64b1;
  border-radius: 1px;
  outline: none;
  height: 5vh;
  width: 8vw;
  padding: 0;
  overflow: hidden;
  transition: 1s all ease;
}

#primary-button::before {
  background: #3c64b1;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: -1;
  transition: all 0.6s ease;
}

#primary-button:hover {
  background-color: #3c64b1;
  color: white;
}

#link {
  text-decoration: none;
}

#image {
  height: 700px;
  width: 700px;
}

.content {
  padding-top: 40px;
}

